@import url("https://fonts.googleapis.com/css2?family=Baloo+Paaji+2:wght@400;500&display=swap");

.container {
  display: grid;
  grid-template-columns: 300px 300px 300px;
  grid-gap: 50px;
  justify-content: center;
  align-items: center;
  height: 120vh;
  margin: 0;
  background-color: #524eb7;
  font-family: "Baloo Paaji 2", cursive;
}

.card {
  background-color: #222831;
  height: 37rem;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.7);
  color: white;
}

.active {
  background-color: green;
}

.image {
  height: 160px;
  width: 160px;
  border-radius: 50%;
  border: 5px solid #272133;
  margin-top: 20px;
  box-shadow: 0 10px 50px rgba(235, 25, 110, 1);
}

.btn-choose {
  width: 100px;
  height: 50px;
  border-radius: 40px;
  background-color: goldenrod;
  text-transform: uppercase;
  font-weight: 900;
  cursor: pointer;
}

.btn-choose:hover, .btn-ability:hover, .btn-back:hover {
  background-color: greenyellow;
}

.btn-ability {
  width: 200px;
  height: 50px;
  border-radius: 40px;
  background-color: goldenrod;
  text-transform: uppercase;
  font-weight: 900;
  cursor: pointer;
}

.hide-card {
  display: none;
}

.btn-back {
  width: 200px;
  height: 50px;
  border-radius: 40px;
  background-color: goldenrod;
  text-transform: uppercase;
  font-weight: 900;
  cursor: pointer;
  text-decoration: none;
  text-align: center;
  justify-content: center;
  display: flex;
}

.btn-disable {
  cursor: not-allowed;
  pointer-events: none;

  width: 200px;
  height: 50px;
  border-radius: 40px;
  text-transform: uppercase;
  font-weight: 900;

  color: gray;
  background-color: #ffffff;
}